<template>
    <div class="w-screen h-screen bg-black bg-opacity-50 fixed-top flex justify-content-center align-items-center z-20"
         @click.self="cancel">
        <div class="bg-gray-400 rounded-3 grid grid-cols-3 p-2 gap-2 z-30">
            <div v-if="example" class="p-2 col-span-full bg-white border border-primary rounded-3 text-2xl flex justify-center text-center">
                {{ example }}
            </div>
            <div class="p-2 col-span-full bg-white border border-primary rounded-3 text-2xl flex justify-center">
                {{ my_data.length === 0 ? '&nbsp;' : my_data.toString() }}
            </div>
            <div class="key key-bg" @click="add('7')">7</div>
            <div class="key key-bg" @click="add('8')">8</div>
            <div class="key key-bg" @click="add('9')">9</div>
            <div class="key key-bg" @click="add('4')">4</div>
            <div class="key key-bg" @click="add('5')">5</div>
            <div class="key key-bg" @click="add('6')">6</div>
            <div class="key key-bg" @click="add('1')">1</div>
            <div class="key key-bg" @click="add('2')">2</div>
            <div class="key key-bg" @click="add('3')">3</div>
            <div class="key key-bg" @click="add('0')">0</div>
            <div class="key key-bg" @click="add('00')">00</div>
            <div class="key key-bg" @click="add('.')">.</div>
            <div class="key bg-red-500" @click="cancel">取消</div>
            <div class="key key-bg" @click="clear">清除</div>
            <div class="key bg-green-500" @click="ok">確認</div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Keyboard",
    props: {
        id: {
            default: ''
        },
        example: {
            default: ''
        }
    },
    data() {
        return {
            my_data: '',
        }
    },
    methods: {
        add(num) {
            this.my_data += num;
        },
        clear() {
            this.my_data = '';
        },
        ok() {
            this.$emit('ok', {data: this.my_data, id: this.id});
        },
        cancel() {
            this.$emit('cancel');
        }
    }
}
</script>

<style scoped>
.keyboard {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 50;
    height: 45vh;
    width: 25vw;
    background-color: #A2AEBC;
    padding: 1rem;
}
.key {
    @apply p-2 flex justify-center border-2 border-white text-white rounded-lg text-2xl;
}
.key-bg {
    @apply bg-black
}
</style>